import { useState } from 'react'
import { Helmet } from 'react-helmet-async'

import { Box, Container, Tab, Tabs } from '@mui/material'

import ManyMemberMigrationCard from './ManyMemberMigrationCard'
import OneMemberMigrationCard from './OneMemberMigrationCard'

interface TabPanelProps {
  children?: React.ReactNode
  index: number
  value: number
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index } = props

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
    >
      {value === index && <Box sx={{ p: 1 }}>{children}</Box>}
    </div>
  )
}

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`
  }
}

const MigrationPage: React.FC = () => {
  const [value, setValue] = useState(0)
  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue)
  }

  return (
    <Container
      maxWidth="lg"
      sx={{
        height: '80vh',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
      }}
    >
      <Helmet>
        <title>회원 추가</title>
      </Helmet>
      <Box width="400px" height="500px">
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <Tabs
            value={value}
            onChange={handleChange}
            variant="fullWidth"
            aria-label="tabs"
            textColor="secondary"
            indicatorColor="primary"
          >
            <Tab label="개별 회원 추가" {...a11yProps(0)} sx={{ padding: '10px!important' }} />
            <Tab label="한번에 회원 추가" {...a11yProps(1)} sx={{ padding: '10px!important' }} />
          </Tabs>
        </Box>
        <TabPanel value={value} index={0}>
          <OneMemberMigrationCard />
        </TabPanel>
        <TabPanel value={value} index={1}>
          <ManyMemberMigrationCard />
        </TabPanel>
      </Box>
    </Container>
  )
}

export default MigrationPage
